import { genFileInfo } from '../utils/fileUtils';
import { getCurrentUser } from '@nextcloud/auth';
import { props } from './DavRequest';
import allowedMimes from './AllowedMimes';
import client from './DavClient';
/**
 * Get tagged files based on provided tag id
 *
 * @param {number} id the tag id to filter
 * @param {Object} [options] optional options for axios
 * @returns {Array} the file list
 */

export default async function (id, options = {}) {
  options = Object.assign({
    method: 'REPORT',
    data: "<?xml version=\"1.0\"?>\n\t\t\t<oc:filter-files\n\t\t\t\txmlns:d=\"DAV:\"\n\t\t\t\txmlns:oc=\"http://owncloud.org/ns\"\n\t\t\t\txmlns:nc=\"http://nextcloud.org/ns\"\n\t\t\t\txmlns:ocs=\"http://open-collaboration-services.org/ns\">\n\t\t\t\t<d:prop>\n\t\t\t\t\t".concat(props, "\n\t\t\t\t</d:prop>\n\t\t\t\t<oc:filter-rules>\n\t\t\t\t\t<oc:systemtag>").concat(id, "</oc:systemtag>\n\t\t\t\t</oc:filter-rules>\n\t\t\t</oc:filter-files>"),
    details: true
  }, options);
  const prefixPath = "/files/".concat(getCurrentUser().uid);
  const response = await client.getDirectoryContents(prefixPath, options);
  return response.data.map(data => genFileInfo(data)) // filter out unwanted mime because server REPORT service only support
  // hardcoded props and mime is not one of them
  // https://github.com/nextcloud/server/blob/5bf3d1bb384da56adbf205752be8f840aac3b0c5/apps/dav/lib/Connector/Sabre/FilesReportPlugin.php#L274
  .filter(file => file.mime && allowedMimes.indexOf(file.mime) !== -1) // remove prefix path from full file path
  .map(data => Object.assign({}, data, {
    filename: data.filename.replace(prefixPath, '')
  }));
}